body {
  margin: 0;
  padding: 0;
}

#root {
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

#game {
    max-width: 100%;
    max-height: 100%;
    display: block;
    margin: 0 auto;
    background: #6cf;
}

.Home {
  text-align: center;
  overflow: hidden;
}
.Home>img {
  max-width: 100%;
  max-height: 100%;
}
.Home .button {
  cursor: pointer;
  position: absolute;
}
.Home #start {
  left: 50%;
  top: 50%;
  margin-left: -161px;
  margin-top: -37.5px;
}

.Home #setting {
  position: absolute;
  right: 3em;
  bottom: 1em;
  color: #fff;
  font-weight: bold;
}

.PIXI.UI {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    overflow: hidden;
    z-index: 1;
}

.Scene0 .boats>* {
    position: absolute;
}
.Scene0 .boats>div>span {
    position: absolute;
    white-space: nowrap;
    color: #fff;
    right: 130px;
    height: 110px;
    line-height: 110px;
    font-size: 36px;
    font-weight: bold;
    text-shadow: 0px -3px 3px #1d3e12, 0px 3px 3px #1d3e12, -3px 0px 3px #1d3e12, 3px 0px 3px #1d3e12;
}

.Scene0>.members>* {
    position: absolute;
    white-space: nowrap;
    color: #fff;
    font-size: 32px;
    font-weight: bold;
    -webkit-text-stroke: 1px #000;
    text-shadow: 0px -3px 3px #000, 0px 3px 3px #000, -3px 0px 3px #000, 3px 0px 3px #000;
}
.Scene0>.members>div>img {
    position: absolute;
    max-width: 150px;
    left: -20px;
    top: -35px;
    z-index: -1;
}

.Setting {
    overflow: auto;
}

.Setting .titleBar {
    padding: 1em;
}
.Setting .titleBar>* {
    display: inline-block;
    margin: 0 0.5em;
    height: 1.5em;
    line-height: 1.5em;
}
.Setting>.titleBar>* {
    color: #fff;
    font-weight: bold;
    background: #6CF;
    border-radius: 1em;
    padding: 0.2em 1em;
}
.Setting .titleBar>#insert {
    padding: 0.2em 0.5em;
}
.Setting .titleBar>#insert>* {
    vertical-align: middle;
    margin: 0 0.5em;
}
.Setting .titleBar>#save,
.Setting .titleBar>#insert>#commit {
    cursor: pointer;
}

.Setting thead>tr>th.padding,
.Setting tbody>tr>td.padding {
    width: 1em;
}

.Setting thead>tr>th {
    background: #000;
    color: #fff;
    font-weight: bold;
    font-size: 1.2em;
    padding: 0.2em 1em;
}
.Setting tbody>tr:nth-child(even) {
    background: #ddd;
}
.Setting tbody>tr>td:nth-child(1),
.Setting tbody>tr>td:nth-child(7),
.Setting tbody>tr>td:nth-child(13) {
    background: #6CF;
    color: #fff;
    text-align: right;
    padding-right: 0.5em;
}

.Setting .titleBar input.name,
.Setting tbody>tr>td input.name {
    text-align: center;
    width: 4em;
}

